@font-face {
    font-family: 'Montserrat';
    src:
    url('../font/Montserrat-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

body, html {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
    overflow: hidden;
    background-color: #FFEA82;

}
#canvas-container {
    height: 100vh;
    width: 100vw;
}
#overlay {
    position: relative;
    width: 100%;
    height: 100vh; /* Use full viewport height */
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

#overlay-img {
    max-width: 75%; /* Maximum of 75% of viewport width */
    max-height: 75%; /* Maximum of 75% of viewport height */
    width: auto;
    height: auto;
    position: relative; /* Centered relative to its container */
    object-fit: contain; /* Ensure the aspect ratio is preserved */
}
#overlay-text {

    position: absolute; /* Ensure the text is positioned over the image */
    z-index: 2; /* Ensure the text appears above the image */
    user-select: none; /* Prevent text selection */
    -webkit-user-select: none; /* Chrome/Safari */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+ */
    font-size: 3em;
    color: #FFFFFF;
    text-shadow:
            -1px -1px 5px  #507177,
            1px -1px 5px #507177,
            -1px  1px 5px #507177,
            1px  1px 5px #507177;

            /*5px 5px 5px #507177; !* Optional: Add text shadow for better readability *!*/
}
